import React, { Component } from "react";
import moment from "moment";
import "./Table.css";
import { formatAmount } from "./../utils/format";

class TableList extends Component {
  state = {
    startDate: "2017-08-15",
    tableData: []
  };
  componentDidMount() {
    // https://www.cnblogs.com/candy-Yao/p/9945306.html
    // 获取差值
    let diff = moment().diff(moment(this.state.startDate));
    // 获取当前期数
    let index = Math.ceil(moment.duration(diff).asMonths());
    this.setTableData(index);
  }
  setTableData = num => {
    console.warn(num);
    let arr = [];

    for (let i = -5; i < 5; i++) {
      const date = moment()
        .add(i, "month")
        .format("YYYY-MM-15");

      let amount = 0;
      if (i < 1) {
        amount = 6879.15 * (num + i);
      }
      const amount2 = 1238200 - amount;
      arr.push({
        index: num + i,
        date,
        amount,
        amount2
      });
    }
    this.setState({
      tableData: arr
    });
  };
  render() {
    return (
      <table className="table">
        <thead>
          <tr>
            <th>期数</th>
            <th>日期</th>
            <th>已还金额</th>
            <th>剩余金额</th>
          </tr>
        </thead>
        <tbody>
          {this.state.tableData.map(item => {
            return (
              <tr key={item.index}>
                <td>{item.index}</td>
                <td>{item.date}</td>
                <td>{formatAmount(item.amount)}</td>
                <td>{formatAmount(item.amount2)}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    );
  }
}

export default TableList;
